.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 50px 0;

}
.card{
    margin: 0 2% !important;
    font-style: oblique;
}

.infected{
    border-bottom:10px solid #3457D5;
}
.recovered{
    border-bottom:10px solid #32de84;
}
.deaths{
    border-bottom:10px solid #fd5c63;
}

@media only screen and (max-width: 770px) {
    .card {
      margin: 2% 0 !important;

    }
  }